.checkbox, .radio, .switch {
  color: $blue;
  margin: 0 0 12px 0;
  display: table;
  cursor: pointer; }

.checkbox.inline, .radio.inline, .switch.inline {
  margin: 0 12px 0 0;
  display: inline-block; }

.checkbox input, .radio input, .switch input {
  display: none; }

.checkbox input + span, .radio input + span, .switch input + span {
  color: #333;
  height: 22px;
  font-size: 14px;
  font-weight: bold;
  position: relative;
  display: block; }

.checkbox input + span {
  &:before, &:after {
    content: '';
    display: block;
    left: 0;
    top: 0;
    position: absolute; } }

.radio input + span {
  &:before, &:after {
    content: '';
    display: block;
    left: 0;
    top: 0;
    position: absolute; } }

.switch input + span {
  &:before, &:after {
    content: '';
    display: block;
    left: 0;
    top: 0;
    position: absolute; } }

.checkbox input + span:before, .radio input + span:before, .switch input + span:before {
  height: 22px;
  border: 2px solid $blue;
  background: #fff;
  -webkit-transition: background .3s ease, border-color .3s ease;
  transition: background .3s ease, border-color .3s ease; }

.checkbox input + span:after, .radio input + span:after, .switch input + span:after {
  -webkit-transition: opacity .2s ease,  background .2s ease, -webkit-transform .3s ease;
  transition: opacity .2s ease,  background .2s ease, -webkit-transform .3s ease;
  transition: transform .3s ease, opacity .2s ease,  background .2s ease;
  transition: transform .3s ease, opacity .2s ease,  background .2s ease, -webkit-transform .3s ease; }

.checkbox input:checked + span:before, .radio input:checked + span:before, .switch input:checked + span:before {
  background: $blue;
  border-color: $blue; }

.checkbox input:checked + span:after, .radio input:checked + span:after, .switch input:checked + span:after {
  -webkit-transition: opacity 0.3s ease, background 0.3s ease, -webkit-transform 0.6s cubic-bezier(0.175, 0.88, 0.32, 1.2);
  transition: opacity 0.3s ease, background 0.3s ease, -webkit-transform 0.6s cubic-bezier(0.175, 0.88, 0.32, 1.2);
  transition: opacity 0.3s ease, background 0.3s ease, transform 0.6s cubic-bezier(0.175, 0.88, 0.32, 1.2);
  transition: opacity 0.3s ease, background 0.3s ease, transform 0.6s cubic-bezier(0.175, 0.88, 0.32, 1.2), -webkit-transform 0.6s cubic-bezier(0.175, 0.88, 0.32, 1.2); }

.checkbox:hover input:not(:checked) + span:before, .radio:hover input:not(:checked) + span:before, .switch:hover input:not(:checked) + span:before {
  border-color: $blue; }

.checkbox input + span, .radio input + span {
  padding-left: 22px; }

.checkbox input + span:not(:empty), .radio input + span:not(:empty) {
  padding-left: 30px; }

.checkbox input + span:before, .radio input + span:before {
  width: 22px; }

.checkbox input + span:after, .radio input + span:after {
  opacity: 0; }

.checkbox input:checked + span:after, .radio input:checked + span:after {
  opacity: 1; }

.checkbox input {
  + span {
    &:before {
      border-radius: 5px; }
    &:after {
      width: 5px;
      height: 9px;
      border: 3px solid #fff;
      border-top: 0;
      border-left: 0;
      left: 8px;
      top: 5px;
      transform: rotate(20deg); } }

  &:checked + span:after {
    -webkit-transform: rotate(43deg) translate(1px, 0);
    transform: rotate(43deg) translate(1px, 0); } }

.radio input {
  + span {
    &:before {
      border-radius: 50%; }

    &:after {
      border-radius: 50%;
      width: 26px;
      height: 26px;
      background: $blue;
      opacity: 0;
      -webkit-transform: scale(0.6);
      transform: scale(0.6); } }

  &:checked + span:after {
    background: #fff;
    -webkit-transform: scale(0.4);
    transform: scale(0.4); } }
.switch input:checked + span:after {
  background: #fff!important; }
