.switch {
  input {
    + span {
      padding-left: 38px;

      &:not(:empty) {
        padding-left: 46px; }

      &:before {
        width: 38px;
        height: 20px;
        border-radius: 15px; }

      &:after {
        left: 4px;
        top: 4px;
        border-radius: 50%;
        width: 16px;
        height: 16px;
        background: $blue; } }

    &:checked + span:after {
      background: $blue;
      -webkit-transform: translateX(16px) scale(0.9);
      transform: translateX(16px) scale(0.9); } }

  &:hover input:not(:checked) + span:after {
    background: $blue; } }
/* form starting stylings ------------------------------- */
.fieldgroup {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  padding-top: 30px;
  border-left: none;
  border-right: none;
  border-bottom: none;
