.group {
  position: relative;
  margin-bottom: 25px;
  & span.error {
    font-size: 0.8rem;
    margin-top: -30px; }
  input, textarea {
    font-size: 16px;
    padding: 10px;
    display: block;
    width: 100%;
    &:focus {
      outline: none; } }
  label {
    color: #999;
    font-size: 1rem;
    position: absolute;
    pointer-events: none;
    left: 10px;
    top: 10px;
    transition: 0.2s ease all;
    &.is_active {
      top: -20px;
      font-size: 14px;
      color: $blue; } }
  input {
    &:focus ~ label {
      top: -20px;
      font-size: 14px;
      color: $blue; } }
  textarea {
    padding-top: 20px;
    &:focus ~ label {
      top: -20px;
      font-size: 14px;
      color: $blue; } }
  .bar {
    position: relative;
    display: block;
    width: 320px;
    &:before,
    &:after {
      content: '';
      height: 2px;
      width: 0;
      bottom: 0;
      position: absolute;
      background: $blue;
      transition: 0.2s ease all;
      -moz-transition: 0.2s ease all;
      -webkit-transition: 0.2s ease all; }
    &:before {
      left: 50%; }
    &:after {
      right: 50%; } }
  input:focus {
    ~ .bar:before,
    ~ .bar:after {
      width: 50%; } } }
