    //     position: absolute
    //     right: 17px
    //     top: 10px
    //     padding: 0px 5px
    //     color: #2D7FA8

    //   &:before
    //     content: '\f07c'
    //     font-family: FontAwesome
    //     position: absolute
    //     left: 17px
    //     top: 10px
    //     padding: 0px 5px
 } }    //     color: #2D7FA8
#footer_nav {
  position: fixed;
  display: none;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: #fff;
  box-shadow: 0 15px 15px 5px rgba(38,59,94,0.8);
  transform: translateY(100px);
  transition: all .3s ease;
  z-index: 9999;
  &.active {
    transform: translateY(0px);
    transition: all .3s ease; }
  & ul {
    padding: 0;
    margin: 0;
    display: flex;
    color: $title_col;
    justify-content: space-between;
    list-style: none;
    & li {
      height: 100%;
      line-height: 50px;
      font-size: 2rem;
      position: relative;
      text-align: center;
      width: 100%;
      &:last-child {
        &::before {
          display: none; } }
      &::before {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        background-color: #ddd;
        width: 2px;
        height: 100%; }
      & .search {
        position: relative;
        float: none;
        width: 100%;
        padding: 0;
        color: $title_col;
        background-color: transparent; }
      & a {
        color: $title_col;
        font-weight: 600; } } } }
