.promo {
  width: 100%;
  height: 100%;
  color: #fff;
  padding: 50px 0;
  min-height: 300px;
  text-align: center;
  position: relative;
  overflow: hidden;
  background-attachment: fixed;
  background-size: cover;
  background-position: top center;
  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: calc(100% - -10px);
    height: calc(100% - -10px);
    background-color: rgba($green,0.4);
    background-attachment: fixed;
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
    background-image: url('../img/count_section.jpg');
    // filter: blur(6px)
    //opacity: 0.2
    z-index: -2; }
  &::after {
    content: '';
    position: absolute;
    left: 0;
