.feed {
  &-list {}

  &-head {
    // text-align: center
    height: 80px;
    line-height: 80px;
    border-bottom: 1px solid #ededed;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    &-item {} }

  &-detail {
    list-style: none;
    padding: 0;
    margin: 0;
    display: inline-block;
    width: 100%;
    .file {
      &_list {
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        list-style: none;
        gap: 15px; }
      &_item {
        width: 100%;
        &:last-child {
          .file_item_link {
            border: none; } }
        &_link {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          width: 100%;
          height: 50px;
          border-bottom: 1px solid #eee;
          gap: 15px;
          &_icon {
            font-size: 2rem;
            width: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
              width: 90%;
              height: auto;
              display: block; } }
          &_name {
            width: 100%;
            color: #333; } } } }
    &-status {
      &-line {
        display: flex;
        padding: 0 40px 0 0;
        margin: 20px 0;
        flex-direction: row;
        justify-content: flex-start;
        list-style: none;
        & li {
          position: relative;
          cursor: pointer;
          width: 100%;
          height: 45px;
          padding-left: 30px;
          background-color: #dcdcdc;
          line-height: 45px;
          color: $title_col;
          margin-right: 0px;
          transition: all .3s ease;
          &:first-child {
            border-radius: 5px 0 0 5px; }
          &.is_none {
            &[data-status-id] {
              background-color: #dcdcdc;
              color: $title_col;
              &:before {
                border-color: transparent transparent transparent #dcdcdc; } } }
          &:hover {
            transition: all .3s ease;
            box-shadow: 4px 3px 3px rgba(#000,0.4);
            &:after {
              opacity: 1;
              transition: all .3s ease; } }
          &:before {
            content: '';
            position: absolute;
            top: 0;
            right: -25px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 22.5px 0 22.5px 25px;
            border-color: transparent transparent transparent #dcdcdc; }
          &:after {
            content: '';
            opacity: 0;
            position: absolute;
            top: 5px;
            right: -28px;
            width: 0;
            height: 0;
            filter: blur(1px);
            border-style: solid;
            border-width: 21.5px 0 21.5px 24px;
            border-color: transparent transparent transparent rgba(#000,0.4);
            transition: all .3s ease;
            z-index: -1; }
          &[data-status-id="15"] {
            background-color: rgba($red,0.2);
            color: $red;
            z-index: 3;
            &:before {
              border-color: transparent transparent transparent #fee2e4; } }
          &[data-status-id="16"] {
            background-color: rgba($yellow,0.2);
            color: $yellow;
            z-index: 2;
            &:before {
              border-color: transparent transparent transparent #fdf1d0; } }
          &[data-status-id="17"] {
            background-color: rgba($green,0.2);
            color: $green;
            z-index: 1;
            &:before {
              border-color: transparent transparent transparent #ccf4eb; } } } } }
    & .group {
      margin-bottom: 30px; }
    &-title {
      text-transform: uppercase;
      margin-bottom: 30px; }
    & textarea {
      float: left;
      width: 100%;
      min-height: 100px;
      padding: 15px;
      margin-bottom: 15px; }
    &-value {
      float: left;
      width: 100%;
      max-width: 50%;
      &-name {
        float: left;
        width: 100%;
        max-width: 50%;
        font-weight: bold;
        border: 1px soled #333; } }
    & .popup-gallery {
      float: left;
      margin-bottom: 20px;
      a {
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        width: 160px;
        height: 120px; } }
    &-left {
      position: sticky;
      top: 0; }
    &-right {
      width: 100%;
      height: auto;
      overflow-y: auto;
      position: relative;
      .btn {
        background-color: $blue;
        color: #fff;
        border-radius: 5px;
        padding: 10px 25px;
        // width: 100%
        align-self: self-start; } }
    &-action {
      position: sticky;
      top: 0;
      display: flex;
      flex-direction: column;
      background-color: #fff;
      width: 100%;
      height: auto;
      min-height: 50px;
      margin-bottom: 20px;
      padding: 0 20px;
      box-shadow: 2px 0px 3px rgba(#000,.2);
      z-index: 20;
      &-list {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        list-style: none;
        width: 100%;
        height: 50px; }
      &-item {
        cursor: pointer;
        font-size: 1.5rem; }
      &-content {
        position: relative;
        width: 100%;
        height: auto;
        padding-bottom: 20px;
        .responsible_search {
          position: relative;
          &_list {
            position: absolute;
            top: 100%;
            left: 0;
            background-color: #fff;
            box-shadow: 0px 5px 7px rgba(#000,.2);
            width: 100%;
            li {
              display: flex;
              flex-direction: column;
              padding: 5px 15px;
              cursor: pointer;
              border-bottom: 1px solid #eee;
              &:hover {
                background-color: #eee; }
              span {
                font-size: 0.8rem; } } } } } } }

  &-item {
    cursor: pointer;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    height: 80px;
    position: relative;
    color: rgba($title_col,0.6);
    border-radius: 5px;
    margin: 15px 0;
    transition: all .3s ease;
    &-toggle {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0; }
    &.active {
      height: 380px;
      box-shadow: 0 5px 14px rgba(#000,.1);
      & .feed-item-prop {
        display: block; }
      & .feed-item {
        &-prop {
          margin-top: 15px; }
        &-departament {
          text-overflow: ellipsis;
          white-space: normal;
          max-width: 100%; }
        &-left {
          flex-direction: column;
          justify-content: space-between;
          transition: all .3s ease; }
        &-left, &-right {
          width: 50%; }
        &-right {
          display: block; } }
      &::before {
        height: 100%; } }
    &:hover {
      transition: all .3s ease;
      color: rgba($title_col,0.6);
      box-shadow: 0 5px 14px rgba(#000,.1); }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 3px;
      height: 0;
      margin: auto;
      background-color: $green; }
    &-left {
      padding: 15px;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      transition: all .3s ease;
      width: 100%; }
    &-right {
      padding: 15px;
      opacity: 0; }
    &-avatar {
      margin: 0 15px;
      width: 50px;
      height: 50px;
      line-height: 50px;
      border-radius: 100%;
      display: block;
      text-align: center;
      background-color: #ededed; }
    &-departament {
      max-width: 180px;
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #393738;
      font-size: 17px;
      font-weight: 500;
      position: relative; }
    &-prop {
      text-transform: uppercase;
      color:  #dfdfdf;
      display: none;
      font-size: 1rem; }
    &-text {
      width: 100%;
      & .text {
        min-height: 100px;
        padding: 15px;
        background-color: #d5d5d5;
        border-radius: 5px; } } } }
.print_btn {
  position: absolute;
  right: 170px;
  bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  color: $blue;
  border: 1px solid $blue;
  border-radius: 100%;
  text-align: center;
  line-height: 35px;
  z-index: 200; }
